
<div>
  <div class="w-100 d-flex">
    <b-form-select
      id="strategy-select"
      v-model="locStrategy"
      :options="botStore.activeBot.strategyList"
    >
    </b-form-select>
    <div class="ml-2">
      <b-button @click="botStore.activeBot.getStrategyList">&#x21bb;</b-button>
    </div>
  </div>

  <textarea
    v-if="showDetails && botStore.activeBot.strategy"
    v-model="strategyCode"
    class="w-100 h-100"
  ></textarea>
</div>
